import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-my-practice',
  templateUrl: './my-practice.component.html',
  styleUrls: ['./my-practice.component.css']
})

export class MyPracticeComponent implements OnInit {
  post=[];
  constructor() {
    
    console.log('constructor for MyPracticeComponent');

    fetch("https://jsonplaceholder.typicode.com/posts")
    .then((res)=>res.json())
    .then((data)=>this.post=data)
    .catch((err)=>console.log(err))
    console.log(this.post);

   }

  ngOnInit() {
    console.log('ngOnInit for MyPracticeComponent');
    console.log(this.post);
  }

  testB=345;
  switch_expression=12;

  items=[{
          tId:1,
          name:'ab1'
        },
        {
          tId:2,
          name:'ab2'
        },
        {
          tId:3,
          name:'ab3'
        }
        ];

        addOneMore()
        {
          this.items.push({
            tId:4,
            name:'ab4'
          });

          console.log('addOneMore Called');
        }

}


